/* Local class-based styling */
.table {
    border-collapse: collapse;
    width: 100%;
  }
  
  .table th, .table td {
    text-align: center;
    padding: 10px;
  }
  
  .table thead {
    background-color: #f8f9fa;
  }
  
  .table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .table td {
    vertical-align: middle;
  }
  
  .badge {
    font-size: 14px;
  }
  
  .btn-info {
    font-size: 12px;
  }
  
  .btn-sm {
    font-size: 14px;
  }
  
  .heading {
    color: #333;
  }
  
  .mt-5 {
    margin-top: 50px;
  }
  
  .mb-4 {
    margin-bottom: 30px;
  }
  